import React, { useEffect, useState, useRef } from "react";
import { postVideo } from "../api/videos.api";
// It's not clear to me how to trigger updates to the UI
const useForceUpdate = () => useState()[1];

function VideoUploader() {
	const [description, setDescription] = useState("");
	const [progressValue, setProgressValue] = useState("0%");
	const [isprogressing, setisprogressing] = useState(false);
	const [errorMessage, setErrorMessage] = useState("");
	const [successMessage, setSuccessMessage] = useState("");
	const fileInput = useRef(null);
	const forceUpdate = useForceUpdate();

	const pathName = window.location.pathname;
	const match_id = pathName.substring(pathName.lastIndexOf("/") + 1);

	function onUploadProgress(progressEvent) {
		var percentCompleted = Math.round(
			(progressEvent.loaded * 100) / progressEvent.total
		);
		setProgressValue(`${percentCompleted}%`);
		console.log(percentCompleted);
	}
	async function onSubmit() {
		setisprogressing(true);
		setProgressValue("0%");
		setSuccessMessage("");
		setErrorMessage("");
		const matchId = match_id;
		const desc = description;
		const file = fileInput.current.files[0];
		try {
			const response = await postVideo(
				matchId,
				desc,
				file,
				onUploadProgress
			);
			if (response.status == 200) {
				setSuccessMessage("Video uploaded");
				setErrorMessage("");
			}
		} catch (e) {
			setErrorMessage(e.message + "!");
		}
		setisprogressing(false);
	}

	const isBtnSubmitDisable = () => {
		if (fileInput.current == null) {
			return true;
		}
		if (fileInput.current.files.length > 0) {
			return false;
		}
		return true;
	};

	const onDescriptionChangeHandler = (event) => {
		setDescription(event.target.value);
	};
	return (
		<div>
			<div class="container mt-5">
				<nav aria-label="breadcrumb">
					<ol class="breadcrumb breadcrumb-links breadcrumb-dark">
						<li class="breadcrumb-item">
							<a href="/">
								<i class="fas fa-home"></i>
							</a>
						</li>
						{/*<li class="breadcrumb-item">
						<a href="#">Dashboards</a>
					</li>*/}
						<li class="breadcrumb-item active" aria-current="page">
							Video-Upload
						</li>
					</ol>
				</nav>
			</div>
			<div class="container mt-5 card p-4">
				<h2 class="mt-4">Upload Video</h2>
				{errorMessage != "" && (
					<div class="alert alert-danger" role="alert">
						<strong>Error!</strong> {errorMessage}
					</div>
				)}
				{successMessage != "" && (
					<div class="alert alert-success" role="alert">
						<strong>Success!</strong> {successMessage}
					</div>
				)}

				<div class="mt-3">
					<span class="float-left font-weight-bold">MatchID: </span>{" "}
					<span class="float-left ml-2">{match_id}</span>
				</div>
				<div class="form-group">
					<label class="float-left mt-3 font-weight-bold">
						Description
					</label>
					<textarea
						value={description}
						onChange={onDescriptionChangeHandler}
						class="form-control"
						placeholder="Description of Match"
						id="exampleFormControlTextarea1"
						rows="3"
					></textarea>
				</div>

				<div>
					<label class="float-left mt-3 font-weight-bold">
						Select Video
					</label>
					<div class="custom-file">
						<input
							type="file"
							class="custom-file-input float-left"
							ref={fileInput}
							onChange={forceUpdate}
							lang="en"
							accept="video/*"
						/>
						<label
							class="custom-file-label float-left"
							for="customFileLang"
						></label>
					</div>
				</div>

				{isprogressing && (
					<div>
						<div class="progress-info">
							<div class="progress-label">
								{/*<span>Task completed</span>*/}
							</div>
							<div class="progress-percentage">
								<span>{progressValue}</span>
							</div>
						</div>
						<div class="progress">
							<div
								class="progress-bar bg-primary"
								style={{ width: progressValue }}
								role="progressbar"
								aria-valuemin="0"
								aria-valuemax="100"
							></div>
						</div>
					</div>
				)}

				{/*<div class="lds-ripple"><div></div><div></div></div>*/}
				<div class="d-flex justify-content-center">
					<button
						disabled={isBtnSubmitDisable()}
						onClick={() => onSubmit()}
						class="btn btn-primary btn-upload mt-3"
						type="button"
					>
						Submit
					</button>
				</div>
			</div>
		</div>
	);
}

export default VideoUploader;