import React, { useEffect, useState, useRef } from "react";
import { useLocation } from "react-router-dom";
import {
	getVideoEvents,
	getEventTypes,
	getShotType,
	getMatchPlayer,
} from "../api/videos_events";
import Select from "./select";
import { Link } from "react-router-dom";
import utils from "../utils";
import Pagination from "./pagination";
import constant from "../constant";

export default function VideoEvents() {
	const [events, setEvents] = useState([]);
	const [eventType, setEventType] = useState({});
	const [shotType, setshotType] = useState({});
	const [players, setPlayers] = useState({});
	const [pagination, setPagination] = useState({});

	const [currentVideo, setcurrentVideo] = useState({});
	const videoRef = useRef();

	const [searchParams, setsearchParams] = useState({
		searchEventType: null,
		searchShotType: null,
		nonStrikerId: null,
		strikerId: null,
		dismissedId: null,
		inning: null,
		over: null,
		ball: null,
		bowlerId: null
	});

	useEffect(() => {
		fetchEvents(matchid);
	}, [searchParams]);

	const location = useLocation();
	const params = new URLSearchParams(location.search);
	const matchid = params.get("matchid");

	useEffect(() => {
		fetchEvents(matchid);
		fetchEventType(matchid);
		fetchShotTypes(matchid);
		fetchMatchPlayer(matchid);
		// videoRef.current.src =
		//     'https://sample-videos.com/video123/mp4/240/big_buck_bunny_240p_10mb.mp4';
	}, []);

	const eventTypeChange = (key) => {
		if (key === "All") {
			key = null;
		}

		setsearchParams({ ...searchParams, searchEventType: key });
		// fetchEvents(matchid);
	};
	const shotTypeChange = (key) => {
		if (key === "All") {
			key = null;
		}
		setsearchParams({ ...searchParams, searchShotType: key });
	};

	const playerChange = (key) => {
		if (key === "All") {
			key = null;
		}
		setsearchParams({ ...searchParams, nonStrikerId: key });
	};

	const strikerChange = (key) => {
		if (key === "All") {
			key = null;
		}
		setsearchParams({ ...searchParams, strikerId: key });
	};
	const dismissedPlayerChange = (key) => {
		if (key === "All") {
			key = null;
		}
		setsearchParams({ ...searchParams, dismissedId: key });
	};
	const bowlerChange = (key) => {
		if (key === "All") {
			key = null;
		}
		setsearchParams({ ...searchParams, bowlerId: key });
	};

	const onPressInningInput = (event) => {
		if (event.key === "Enter") {
			const value = event.target.value;
			setsearchParams({ ...searchParams, inning: value });
		}
	};

	const onPressOverInput = (event) => {
		if (event.key === "Enter") {
			const value = event.target.value;
			setsearchParams({ ...searchParams, over: value });
		}
	};
	const onPressBallInput = (event) => {
		if (event.key === "Enter") {
			const value = event.target.value;
			setsearchParams({ ...searchParams, ball: value });
		}
	};




	async function fetchEvents(matchid, page = 1, perPage = 10) {
		try {
			const response = await getVideoEvents(
				matchid,
				searchParams.searchEventType,
				searchParams.searchShotType,
				searchParams.nonStrikerId,
				searchParams.strikerId,
				searchParams.dismissedId,
				searchParams.inning,
				searchParams.over,
				searchParams.ball,
				searchParams.bowlerId,
				page,
				perPage
			);
			setEvents(response.data.rows);
			setPagination({ currentPage: page, count: response.total });
		} catch (err) {}
	}
	async function fetchEventType(matchid) {
		try {
			const response = await getEventTypes(matchid);
			const eventTypes = response.data;
			setEventType(eventTypes);
		} catch (err) {}
	}

	async function fetchShotTypes(matchid) {
		try {
			const response = await getShotType(matchid);
			setshotType(response.data);
		} catch (err) {}
	}

	async function fetchMatchPlayer(matchid) {
		try {
			const response = await getMatchPlayer(matchid);
			setPlayers(response.data);
		} catch (err) {}
	}

	function onClickVideo(event) {
		videoRef.current.src = `${process.env.REACT_APP_API_BASE_URL}/api/videos/${event.event.videoId}`;
		// 'https://sample-videos.com/video123/mp4/240/big_buck_bunny_240p_10mb.mp4';
		videoRef.current.currentTime = event.event.frameStart / 60.0;
		videoRef.current.ontimeupdate = function() {
			const endtime = event.event.frameEnd / 60.0;
			if (videoRef.current.currentTime > endtime) {
				videoRef.current.pause();
			}
		};

		videoRef.current.endtimeoffset = event.event.frameEnd / 60.0;
		videoRef.current.play();
		window.scrollTo(0, document.body.scrollHeight);
	}

	function onPageClicked(page) {
		fetchEvents(matchid, page, constant.PAGE_SIZE);
	}
	return (
		<div class="container-fluid mt-5">
			<div class="container mt-5">
				<nav aria-label="breadcrumb">
					<ol class="breadcrumb breadcrumb-links breadcrumb-dark">
						<li class="breadcrumb-item">
							<a href="/videos">Videos</a>
						</li>
						{/*<li class="breadcrumb-item">
						<a href="#">Dashboards</a>
					</li>*/}
						<li class="breadcrumb-item active">Video-Events</li>
					</ol>
				</nav>
			</div>
			<h2 class="mt-4">Video Events</h2>
			<div className="table-responsive mt-3">
				<table className="table align-items-center table-flush">
					<thead className="thead-light">
						<tr>
							<th>
								<div>
									<div>Event Type</div>
									<Select
										data={eventType}
										change={eventTypeChange}
									/>
								</div>
							</th>
							<th scope="col" className="sort">
								<div>
									<div>Shot Type</div>
									<Select
										data={shotType}
										change={shotTypeChange}
									/>
								</div>
							</th>
							<th scope="col">
								<div>
									<div>Inning</div>
									<input
										onKeyPress={(event) =>
											onPressInningInput(event)
										}
										type="number"
										class="form-control header-input mt-2"
										placeholder=""
									/>
								</div>
							</th>
							<th
								scope="col"
								className="sort"
								style={{ minWidth: "80px" }}
							>
								<div>
									<div>Over</div>
									<input
										onKeyPress={(event) =>
											onPressOverInput(event)
										}
										type="number"
										class="form-control header-input mt-2"
										placeholder=""
									/>
								</div>
							</th>
							<th scope="col" className="sort">
								<div>
									<div>Ball</div>
									<input
										onKeyPress={(event) =>
											onPressBallInput(event)
										}
										type="number"
										class="form-control header-input mt-2"
										placeholder=""
									/>
								</div>
							</th>
							<th scope="col" className="sort">
								<div>
									<div>NonStriker Name</div>
									<Select
										data={players}
										change={playerChange}
									/>
								</div>
							</th>
							<th scope="col" className="sort">
								<div>
									<div>Striker Name</div>
									<Select
										data={players}
										change={strikerChange}
									/>
								</div>
							</th>
							<th scope="col" className="sort">
								<div>
									<div>Player Dismissed Name</div>
									<Select
										data={players}
										change={dismissedPlayerChange}
									/>
								</div>
							</th>
							<th scope="col" className="sort">
								<div>
									<div>Bowler Name</div>
									<Select
										data={players}
										change={bowlerChange}
									/>
								</div>
							</th>

							<th scope="col" className="sort"></th>
						</tr>
					</thead>
					<tbody className="list">
						{events.map((event, index) => (
							<tr>
								<td>{event.event.eventTypeName}</td>
								<td>{event.event.shotTypeName}</td>
								<td>{event.ball.inning}</td>
								<td>{event.ball.over}</td>
								<td>{event.ball.ball}</td>
								<td>{event.ball.nonStrikerName}</td>
								<td>{event.ball.strikerName}</td>
								<td>{event.ball.playerDismissedName}</td>
								<td>{event.ball.bowlerName}</td>
								<td>
									<button
										onClick={() => onClickVideo(event)}
										class="btn-icon"
										type="button"
									>
										<i class="ni ni-button-play"></i>
									</button>
									{/*<a class="btn-icon" href={`/video/${video.videoId}`}>
                    <i class="ni ni-button-play"></i>
                  </a>*/}
								</td>
							</tr>
						))}
					</tbody>
				</table>
			</div>
			<nav class="mt-2">{Pagination(pagination, onPageClicked)}</nav>
			<div class="modal-body content-video">
				<video ref={videoRef} class="frame-video" controls>
					{/*<source
						src="http://clips.vorwaerts-gmbh.de/VfE_html5.mp4"
						type="video/mp4"
					/>*/}
					{/*<source src="mov_bbb.ogg" type="video/ogg">*/}
					Your browser does not support HTML video.
				</video>
			</div>
		</div>
	);
}
{
	/*src={`http://127.0.0.1:8080/api/videos/${currentVideo.event.videoId}`}*/
}