import React, { useEffect, useState } from "react";
import { getVideos, deleteVideo } from "../api/videos.api";
import Pagination from "./pagination";
import utils from "../utils";

import constant from "../constant";
import Alert from "./alert.js";
import { Link } from "react-router-dom";

function Videos() {
  const [videos, setVideos] = useState([]);
  const [alert, setAlert] = useState({});
  useEffect(() => {
    fetchVideos();
  }, []);

  let currentVideoId = null;


  async function fetchVideos() {
    const response = (await getVideos()).data;
    setVideos(response);
  }

  async function onDeleteVideo(videoId) {
    try {
      const response = await deleteVideo(videoId);
      setAlert({ type: "success", message: "Video deleted" });
      setTimeout(function () {
        setAlert({});
      }, 3000);
      fetchVideos();
    } catch (err) {
      setAlert({ type: "warning", message: err.message });
      setTimeout(function () {
        setAlert({});
      }, 3000);
    }
  }

  return (
    <div class="container mt-5 card">
      {!utils.isEmpty(alert) && (
        <Alert type={alert.type} message={alert.message} />
      )}

      <h2 class="mt-4">Videos</h2>
      <div className="table-responsive mt-3">
        <table className="table align-items-center table-flush">
          <thead className="thead-light">
            <tr>
              <th scope="col"></th>
              <th scope="col">File name</th>
              <th scope="col">Match ID</th>
              <th scope="col">Play</th>
              <th scope="col">Delete</th>
            </tr>
          </thead>
          <tbody className="list">
            {videos.map((video, index) => (
              <tr>
                <td scope="row">
                  <Link
                    class="btn btn-icon "

                    to={{
                      pathname: "/video-events",
                      search: `?matchid=${video.matchId}`,
                    }}
                  >
                    <span class="btn-inner--icon">
                      <i class="far fa-hand-point-right"></i>
                    </span>
                    <span class="btn-inner--text">Events</span>
                  </Link>

                </td>
                <td scope="row">{video.filename}</td>
                <td scope="row">{video.matchId}</td>
                <td>
                  <Link
                    class="btn-icon"
                    to={{
                      pathname: `/video/${video.videoId}`,
                      state: { id: video.videoId },
                    }}
                  >
                    <i class="ni ni-button-play"></i>
                  </Link>
                  {/*<a class="btn-icon" href={`/video/${video.videoId}`}>
                    <i class="ni ni-button-play"></i>
                  </a>*/}
                </td>
                <td>
                  <button
                    onClick={() => {
                      currentVideoId = video.videoId;
                    }}
                    data-toggle="modal"
                    data-target="#videoDeleteModel"
                    class="btn-icon"
                  >
                    <i class="fas fa-trash-alt"></i>
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
        {/*<nav class="mt-2">{Pagination(pagination, onPageClicked)}</nav>*/}
      </div>

      {/*---------------Model----------------------*/}
      <div
        class="modal fade"
        id="videoDeleteModel"
        tabindex="-1"
        role="dialog"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog modal-dialog-centered" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title" id="exampleModalLabel">
                Delete Confirmation
              </h5>
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body text-left">
              Are you sure you want to delete this video?
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-primary" data-dismiss="modal">
                No
              </button>
              <button
                data-dismiss="modal"
                onClick={() => onDeleteVideo(currentVideoId)}
                type="button"
                class="btn btn-danger"
              >
                Yes
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Videos;