import React, { useEffect, useState, useRef } from "react";
import { getMatches, getTeams, getVenues } from "../api/matches.api";
import Pagination from "./pagination"
import utils from '../utils'

import constant from '../constant'
import Select from "./select";
import DatePicker from "react-datepicker";



function Matches() {
  const [matches, setMatches] = useState([]);
  const [pagination, setPagination] = useState({});
  const [teams, setTeams] = useState([]);
  const [venues, setVenues] = useState([]);


  const [searchParams, setsearchParams] = useState({
    team1Id: null,
    team2Id: null,
    venue: null,
    startDate: null
  });

  useEffect(() => {
    fetchMatches(1, constant.PAGE_SIZE);
    fetchTeams();
    fetchVenues();
  }, []);



  useEffect(() => {

    fetchMatches(1, constant.PAGE_SIZE,searchParams.team1Id, searchParams.team2Id, searchParams.venue, searchParams.startDate );
  }, [searchParams]);

  async function fetchTeams() {
    let response = await getTeams();
    setTeams(response.data);
  }

  async function fetchVenues() {
    let response = await getVenues();
    setVenues(response.data);
  }

  const team1Change = (key) => {
    if (key === "All") {
      key = null;
    }

    setsearchParams({ ...searchParams, team1Id: key });
  };

  const venueChange = (key) => {
    if (key === "All") {
      key = null;
    }

    setsearchParams({ ...searchParams, venue: key });
  };


  const team2Change = (key) => {
    if (key === "All") {
      key = null;
    }

    setsearchParams({ ...searchParams, team2Id: key });
  };




  async function fetchMatches(page, perPage, team1Id = null, team2Id = null, venue = null, startDate = null) {
    if (startDate != null) {
      startDate = utils.fortmatDate(startDate, "yyyy-MM-DD");

    }
    const response = (await getMatches(page, perPage, team1Id, team2Id, venue, startDate)).data;
    setMatches(response.rows);
    setPagination({ currentPage: page, count: response.total });
  }

  function onPageClicked(page) {

    fetchMatches(page, constant.PAGE_SIZE, searchParams.team1Id, searchParams.team2Id, searchParams.venue, searchParams.startDate);
  }

  return (

    <div class="container mt-5 card">
      <h2 class="mt-4">Matches</h2>
      <div className="table-responsive mt-3 match-tb">
        <table className="table align-items-center table-flush">
          <thead className="thead-light">
            <tr>
              <th scope="col" className="sort" data-sort="name">
              <div>
                  <div>Team 1</div>
                  <Select
                    data={teams}
                    change={team1Change}
                  />
                </div>

              </th>
              <th scope="col" className="sort" data-sort="budget">
              <div>
                  <div>Team 2</div>
                  <Select
                    data={teams}
                    change={team2Change}
                  />
                </div>

              </th>
              <th scope="col" className="sort" data-sort="status">
              <div>
                  <div>Venue</div>
                  <Select
                    data={venues}
                    change={venueChange}
                  />
                </div>

              </th>
              <th scope="col" className="sort" data-sort="status">
               <div >
               <div>Start Date</div>
               <div class="input-group date-input mt-2">

                      <div class="input-group-prepend">
                          <span class="input-group-text">
                            <i class="ni ni-calendar-grid-58"></i>
                          </span>
                          <DatePicker selected={searchParams.startDate}  onChange={date => setsearchParams({ ...searchParams, startDate: date })} class="form-control datepicker" />
                      </div>

                      {/*<input
                      class="form-control datepicker" placeholder="Select date" type="text" value="06/20/2020" />*/}



                  </div>
               </div>
              </th>
              <th scope="col">

              </th>
            </tr>
          </thead>
          <tbody className="list">
            {matches.map((match, index) => (
              <tr className="gray-row">
                <th scope="row">{match.team1}</th>
                <th scope="row">{match.team2}</th>
                <td>{match.venue}</td>
                <td>{utils.fortmatDate(match.startDate)}</td>
                <td><a href={'/video-upload/' + match.matchId}> <i class="ni ni-cloud-upload-96"></i> </a></td>

              </tr>
            ))}
          </tbody>
        </table>


        <nav class="mt-2">{Pagination(pagination, onPageClicked)}</nav>
      </div>
    </div>
  );
}

export default Matches;